import * as Comlink from 'comlink/dist/esm/comlink'
import { PYODIDE_URL, PYODIDE_INDEX_URL } from "./constants/pyodide";

self.importScripts(PYODIDE_URL)
import { PYTHON_CODE } from "./contents/codeSnips";

function pythonCodeRun() {
  return (pythonCode) => {
    let results = null;
    if(pythonCode == 'print_hello')
      results = self.pyodide.globals.get('print_hello')()
    else if (pythonCode == 'print_hello_loop')
      results = self.pyodide.globals.get('print_hello_loop')()
    else if (pythonCode == 'prime_number')
      results = self.pyodide.globals.get('prime_number')()
    else if (pythonCode == 'read_file')
      results = self.pyodide.globals.get('read_file')()
    return results
  }
}

async function loadPyodideAndPackages() {
  await self.loadPyodide({ indexURL: PYODIDE_INDEX_URL })
  await self.pyodide.loadPackage(['numpy'])
  self.pyodide.runPython(PYTHON_CODE)
}

const pyodideReadyPromise = loadPyodideAndPackages()

const pythonContext = {
  async setup() {
    await pyodideReadyPromise
    this.pythonCodeRun = pythonCodeRun()
  }
}

Comlink.expose(pythonContext)
